            font-size: 13px; } }
    .promo {
        &__header {
            font-size: 50px; } }
    .reasons {
        &__block {
            align-items: flex-start; }
        &__descr {
            width: 300px; } }
    .mobile {
        &__item {
            width: 300px; } }
    .require {
        br {
            display: none; } }
    .footer {
        &__wrapper > div {
            width: 50%;
            &:nth-child(1) {
                width: 30%; }
            &:nth-child(3) {} } }
