@media (max-width:991px) {
    .menu {
        &__item {
            padding-right: 10px; }
        &__link {
            font-size: 10px; } }
    .subheader {
        &__logo {
            margin-top: 20px;
            img {
                width: 100%; } }
        &__official {
            font-size: 11px; }
        &__phone {
            font-size: 20px; }
        &__btn {
            margin-top: 20px;
            width: 100%;
            height: 40px; } }
    .promo {
        &__header {
            font-size: 40px; }
        &__subheader {
            font-size: 30px; }
        &__descr {
            font-size: 15px; } }
    .reasons {
        &__block {
            flex-direction: column;
            align-items: center; }
        &__descr {
            margin-top: 20px; }
        &__round {
            margin: 0; } }
    .mobile {
        &__item {
            width: 225px;
            height: 255px;
            &_subtitle {
                font-size: 15px; } } }
    .choice {
        min-height: 800px;
        img {
            width: 100%; } }
    .cities {
        min-height: 650px; }
    .footer {
        &__wrapper {
            flex-wrap: wrap; }
        &__wrapper>div {
            width: 100%;
            display: flex;
            justify-content: center;
            margin-bottom: 20px;
            &:nth-child(1) {
                width: 100%; }
            &:nth-child(3) {
                width: 100%;
                justify-content: center; } } } }
@media (max-width:767px) {
    .title {
        margin-top: 25px; }
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, 0.8);
        transition: 0.6s all;
        &__active {
            left: 0; }
        &__item {
            padding-right: 0;
            border: none;
            margin: 10px 0; }
        &__link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: $color-white;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &__active {
            span {
                margin-bottom: -2px;
                &:nth-last-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-last-child(2) {
                    display: none; }
                &:nth-last-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &__phone {
            display: block;
            margin-top: 10px;
            color: $color-white;
            text-align: center;
            font-size: 18px;
            font-weight: 400;
            text-transform: uppercase; }
        &__btn {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 200px;
            height: 40px;
            text-align: center;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
        &__header {
            line-height: 40px; } }
    .reasons {
        &__subtitle {
            text-align: center;
            margin-bottom: 10px; } }
    .mobile {
        padding: 20px 0 20px 0; }
    .choice {
        min-height: 745px;
        padding: 30px 0 60px 0; }
    .require {
        padding: 40px 0 40px 0;
        &__block {
            margin: 10px 0; }
        &__title {
            margin-bottom: 10px; } } }
